<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
    >
      <el-table-column
        prop="contractNo"
        label="合同编号"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        width="220"
        :formatter="wwIdFormatter"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="交易客户"
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="仓单货物品种"
        sortable
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="仓单总价值/总现价"
        :formatter="priceFormatter"
        sortable
        width="200"
      >
      </el-table-column>
      <el-table-column
        label="保值方案"
        width="120"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'method')"
            type="text"
            size="small"
          >
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="totalLoan"
        label="对应总放款金额"
        sortable
        width="140"
      >
        <template slot-scope="scope">
          {{tofixed(scope.row.totalLoan)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="tradeEndtime"
        label="仓单交易到期日"
        sortable
        width="130"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.tradeEndtime)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="loanEndtime"
        label="货款到期日"
        sortable
        width="105"
      >
        <template slot-scope="scope">
          {{dateFormat(scope.row.loanEndtime)}}
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="保值方案"
      :visible.sync="methodVisible"
    >
      <el-tabs type="border-card" style="height:200px">
        <el-tab-pane label="保值方案">
          <div class="program">
            需提醒客户追加{{keepValuePlan.x>0 && isFinite(keepValuePlan.x)?tofixed(keepValuePlan.x):"-"}}吨的货物或{{keepValuePlan.y>0?tofixed(keepValuePlan.y):"-"}}元保证金。
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import * as ajaxUrl from '../ajax.config.js';// 引入接口地址

export default {
  data() {
    return {
      searchForm: {
        option: "",
        inputText: ""
      },
      searchParam:{},
      pageType: 4,
      tableData: [],

      currentPage: 1,
      pageSize: 10,
      totalPage: 1,

      methodVisible: false,
      keepValuePlan: {},
    };
  },
  mounted() {
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType) {
      this.searchParam[this.searchForm.option] = this.searchForm.inputText; 
      this.axios.get(ajaxUrl.GETSETTLELIST, {
        params: {
          pageSize: this.pageSize,
          pageNum: this.currentPage,
          search: this.searchParam,
          status: false,
          orderField: orderField,
          orderType: orderType
        }
      }).then(res => {
        if(res.data.resCode == 200){
          this.tableData = res.data.resData;
          this.totalPage = res.data.page.total;
          this.pageSize = res.data.page.pageSize;
          this.searchParam = {};
        }else{
          this.$message.error(res.data.resMsg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    //仓单显示格式转换
    wwIdFormatter(row, column) {
      if (row.warehouseWarrantList.length == 0) {
        return "-";
      } else if (row.warehouseWarrantList.length == 1) {
        return row.warehouseWarrantList[0].wwNo;
      } else if (row.warehouseWarrantList.length > 1) {
        return (
          row.warehouseWarrantList[0].wwNo +
          "~" +
          row.warehouseWarrantList[row.warehouseWarrantList.length - 1].wwNo +
          "(" +
          row.warehouseWarrantList.length +
          ")"
        );
      }
    },
    //价格格式转换
    priceFormatter(row, column) {
      return this.tofixed(row.originalPrice) + " / " + this.tofixed(row.amount * row.goodsPrice);
    },
    //时间格式转换
    dateFormat(data) {
      return this.common.fullTime(data);
    },
    //根据类型打开dialog
    openDialog(row, type) {
      if (type == "method") {
        //打开保值方案面板
        this.methodVisible = true;
        this.keepValuePlan.x = row.originalPrice/row.goodsPrice - row.amount;
        this.keepValuePlan.y = row.originalPrice - row.amount*row.goodsPrice;
      }
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
    //
    tofixed(number) {
      return this.common.toFixed(number);
    }
  },
  components: {
    vSearch
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
</style>



